    monospace;
}

.App {
  text-align: center;
  font-family: 'Staatliches', cursive;
    background-image: url(/static/media/background-blur.2aea27af.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-image: url(/static/media/background2.1df78147.gif);
  box-shadow: 0px 10px 18px -2px rgba(0,0,0,0.44);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

